
import { Options, Vue } from 'vue-class-component';
import { WebviewPathsMessages } from '@/router/types';

import { RBtn } from '@/ui/atoms';

@Options({
  components: {
    RBtn,
  },
})
export default class DetailedError extends Vue {
  get title(): string {
    return (this.$route.query.title as string) ?? 'Что-то случилось';
  }

  get description(): string {
    return this.$route.query.description as string;
  }

  get returnUrl(): string {
    return (this.$route.query.returnUrl as string) ?? WebviewPathsMessages.CLOSE_WEBVIEW;
  }
}
